function app() {
    const buttons = document.querySelectorAll('.buttonfilter');
    const cards = document.querySelectorAll('.tickets');

    function filter(category, items) {
        items.forEach((item) => {
            const isItemFiltred = !item.classList.contains(category);
            isShowAll = category.toLowerCase() === 'all';
            if (isItemFiltred && !isShowAll) {
                item.classList.add('hide')
            } else {
                item.classList.remove('hide')
            }
        })
    }

    buttons.forEach((button) => {
        button.addEventListener('click', () => {
            const carentCategory = button.dataset.filter
            filter(carentCategory, cards)
            
        })
    })
}